import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import Select from 'react-select';
import { yesAndNoList } from 'src/Utils/Enums';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "Product.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    categoryListData: [],
    brandListData: [],
    productListData: [],
    categoryListDataSelectTagSpecific: [],
    brandListDataSelectTagSpecific: [],
    newProductData: {
        categoryId: null,
        brandId: null
    }
};

const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'name',
        enableEditing: true,
    },
    {
        accessorKey: 'categoryId',
        header: 'categoryId',
        enableEditing: true,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue === null ? "N/A" : renderedCellValue)
    },
    {
        accessorKey: 'brandId',
        header: 'brandId',
        enableEditing: true,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue === null ? "N/A" : renderedCellValue)
    },
    
    
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
];


class Product extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchCategoryDataList();
    }

    //default
    componentDidMount() {
        document.title = "Satanlabs - expenses - Product page";
        this.fetchCategoryDataList();
    }

    fetchCategoryDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/category/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const categoryListDataSelectTagSpecific = result.data.map((element) => {
                        return {
                            value: element.id,
                            label: element.name
                        };
                    });
                    this.setState({
                        categoryListData: result.data,
                        categoryListDataSelectTagSpecific
                    });
                    this.fetchBrandDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchBrandDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/brand/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchBrandDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const brandListDataSelectTagSpecific = result.data.map((element) => {
                        return {
                            value: element.id,
                            label: element.name
                        };
                    });
                    this.setState({
                        brandListData: result.data,
                        brandListDataSelectTagSpecific
                    });
                    this.fetchProductDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchBrandDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchProductDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/product/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        productListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    submitCreateProductForm = (event) => {
        event.preventDefault();

        let reqData = {
            name: event.target.inputName.value
        };

        if(this.doesStateContainAllRequiredProductParams() === false) {
            alert("Please fill all unfilled items in the form");
            return;
        }
        reqData.categoryId = this.state.newProductData.categoryId;
        reqData.brandId = this.state.newProductData.brandId;

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateProductApi(reqData);
    }

    doesStateContainAllRequiredProductParams() {
        return (this.state.newProductData.categoryId !== null && this.state.newProductData.brandId !== null);
    }

    callCreateProductApi(newProduct) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/product/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newProduct), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateProductApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callCreateProductApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>PRODUCT &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <br />

                {
                /* Create new product */
                <CCol md="5">
                    <CCardGroup>
                    <CCard className="p-4">
                        <CCardBody>
                        <CForm onSubmit={this.submitCreateProductForm}>
                            <h1>Save a new Product</h1>
                            <CInputGroup className="mb-3">
                                <CInputGroupPrepend>
                                    <CInputGroupText>
                                    <CIcon name="cilPencil" />
                                    </CInputGroupText>
                                </CInputGroupPrepend>
                                <CInput
                                    placeholder="Product Name" 
                                    autoComplete="on"
                                    type="text"
                                    step="any"
                                    id="inputName"
                                    label="Product Name"
                                    required
                                />
                            </CInputGroup>

                            <CIcon name="cilPencil" /> <b>Category :</b>
                            <br />
                            <Select 
                                onChange={(choice) => {
                                    const newProductData = this.state.newProductData;
                                    newProductData.categoryId = choice.value;
                                    this.setState({
                                        newProductData
                                    });
                                }}
                                options={this.state.categoryListDataSelectTagSpecific}
                            />
                            <br />

                            <CIcon name="cilPencil" /> <b>Brand :</b>
                            <br />
                            <Select 
                                onChange={(choice) => {
                                    const newProductData = this.state.newProductData;
                                    newProductData.brandId = choice.value;
                                    this.setState({
                                        newProductData
                                    });
                                }}
                                options={this.state.brandListDataSelectTagSpecific}
                            />
                            <br />

                            <CRow>
                                <CCol xs="6">
                                    <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                </CCol>
                                <CCol xs="6" className="text-right">
                                    <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                </CCol>
                            </CRow>
                        </CForm>
                        </CCardBody>
                    </CCard>
                    </CCardGroup>
                </CCol>    
                }

                <br /><br />

                {
                this.state.isLoaded ?
                    <MaterialReactTable
                        initialState={{ density: 'compact' }}
                        columns={tableColumnData} 
                        data={this.state.productListData}
                        enablePagination = {true}

                        editingMode={"row"}
                        enableEditing
                        onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                    /> : <br/>
                }

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default Product;
